@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Open+Sans:wght@300;400;500;600;700;800&family=Roboto:ital,wght@0,100;0,400;0,900;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: #000;
  height: 100vh;
}
:root{
    --input__font: 'Montserrat', sans-serif;
}

.Input_input__29Zev{
    background-color: #1e1f23;
    border-radius: 15px;
    padding: 20px;
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    place-items: left;
}

input{
    padding: 15px;
    background-color: black;
    border: none;
    border-radius: 15px;
    color: #efc99d;
    font-family: 'Montserrat', sans-serif;
    font-family: var(--input__font);
}


label{
    color: #efc99d;
    font-size: 1.2rem;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    font-family: var(--input__font);
}

button{
    border: none;
    border-radius: 15px;
    padding: 10px 20px;
    color: #1e1f23;
    background-color: #efc99d;
    font-size: 1.2rem;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    font-family: var(--input__font);
}

.Input_input__field__1rNJG{
    width: 100%;
    display: grid;
    grid-gap: 10px;
    gap: 10px;
}
.wrapper{
    width: 80%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
}
